import './Footer.css';

const Footer = ({items, completed, active }) => {
    let result = items.length;
   
    return ( 
        <div className="footer">
            <p>{result} Items left</p>
            <span className="opt" onClick={active}>Active</span><br />
            <span className="opt" onClick={completed}>Completed</span>
        </div>
     );
}
 
export default Footer;